class Firebase {
    
    constructor() {
        app.initializeApp(firebaseConfig);
        this.auth = app.auth();
        this.db = app.firestore;
        this.u_id = 0;
        this.currentUserID = 0;
        this.currentUsername = "";        
    }

    async getTeamLists() {
        console.log("getting team lists...");
        // get a list of all homework from userid
        let db = "teamlists";
        let x = await firebase.database().ref(db).once('value');
        let teamList = [];
        let v;
        for (let k in x.val()) {
            v = x.val()[k];
            teamList = v;
        }
        console.log("done");
        return teamList;
    }

    async getSuperCoachData() {
        console.log("getting supercoach data...");
        // get a list of all homework from userid
        let db = "supercoach";
        let x = await firebase.database().ref(db).once('value');
        let scStats = [];
        let v;
        for (let k in x.val()) {
            v = x.val()[k];
            scStats = v;
        }
        console.log("done");
        return scStats;
    }
}

export default new Firebase();
