import React, {useEffect} from 'react';
import './App.css';
import firebase from './firebase';

const App = () => {
  let teamLists;
  let scStats;
  useEffect(() => {        
    startUp();
  });

  async function startUp() {
    console.log("starting...");
    
    document.getElementById("myHtml").style.display = "none";
    document.getElementsByClassName("checkbox")[0].style.display = "none";
    document.getElementsByClassName("loadingData")[0].style.display = "block";
    document.getElementById("priceBox").checked = true;
    document.getElementById("avgBox").checked = true;
    document.getElementById("beBox").checked = true;

    teamLists = await firebase.getTeamLists();
    scStats = await firebase.getSuperCoachData();
    document.getElementsByClassName("checkbox")[0].style.display = "block";
    document.getElementsByClassName("loadingData")[0].style.display = "none";
    getHtml();
    document.getElementById("myHtml").style.display = "block";
  }
  
  function getPlayerData(nameKey, scStats) {
    for (let p = 0; p < scStats.length; p++) {
      let firstName = scStats[p].first_name;
      let lastName = scStats[p].last_name;
      if (firstName.toLowerCase() + lastName.toLowerCase() === nameKey) {
        return scStats[p];
      }
    }
    let nullPlayer = {
      first_name : nameKey
      , last_name : ""
      , price : 0
      , team : ""
    }
    return nullPlayer;
  }

  function getHtml() {
    let myHtml = "";
    let p = 0;
    let playerNum = 0;
    let t1 = "";
    let t2 = "";
    let nameKey = "";
    let nameKey2 = "";
    let player = "";
    let player2 = "";
    let position = "";
    let team = "";
    let backsMark;
    let forwardsMark;
    let interchangeMark;
    let reservesMark;
    while (p < teamLists.length) {
    //while (p < 40) {
      playerNum += 1;
      team = teamLists[p].Team;
      position = teamLists[p].Position;
      nameKey = teamLists[p].Name;
      nameKey2 = teamLists[p+1].Name;
      player = getPlayerData(nameKey, scStats);
      player2 = getPlayerData(nameKey2, scStats);

      if (team !== t1) {
        // next match
        playerNum = 1;
        t1 = team;
        t2 = teamLists[p+1].Team;
        if (p > 0) {
          myHtml += "</tbody></table><br></div><br>";
        }
        let logo1 = "https://www.nrl.com/client/dist/logos/" + t1.toLowerCase().replace(" ", "-") + "-badge.svg?bust=202103101";
        let logo2 = "https://www.nrl.com/client/dist/logos/" + t2.toLowerCase().replace(" ", "-") + "-badge.svg?bust=202103101";
        myHtml += `
        <div class="match">
          <table class="teams">
            <tbody>
            <tr><td>` + t1 + `</td><td>` + t2 + `</td></tr>
            <tr><td><img src='` + logo1 + `' /></td><td><img src='` + logo2 + `' /></td></tr>
            </tbody>
          </table>    
          <hr>
        `;
        backsMark = 0;
        forwardsMark = 0;
        interchangeMark = 0;
        reservesMark = 0;
      }

      if (position === "Fullback" && backsMark === 0) {
        backsMark = 1;
        myHtml += `
        <h3>BACKS</h3>
        <table>
          <tbody> 
        `;
      }

      if (position === "Prop" && forwardsMark === 0) {
        forwardsMark = 1;
        myHtml += `          
          </tbody>
        </table>
        <hr>
        <h3>FORWARDS</h3>
        <table>
          <tbody> 
        `;
      }

      if (position === "Interchange" && interchangeMark === 0) {
        interchangeMark = 1;
        myHtml += `
          </tbody>
        </table>
        <hr>
        <h3>INTERCHANGE</h3>
        <table>
          <tbody> 
        `;
      }

      if (position === "Reserve" && reservesMark === 0) {
        reservesMark = 1;
        myHtml += `
          </tbody>
        </table>
        <hr>
        <h3>RESERVES</h3>
        <table>
          <tbody> 
        `;
      }

      let price1 = "";
      let price2 = "";
      if (document.getElementById("priceBox").checked === true) {
        price1 = "$" + (parseInt(player.price)/1000).toString() + "k";
        price2 = "$" + (parseInt(player2.price)/1000).toString() + "k";
        if (document.getElementById("avgBox").checked === true || document.getElementById("beBox").checked === true) {
          price1 += ", ";
          price2 += ", ";
        }
      }

      let avg1 = "";
      let avg2 = "";
      if (document.getElementById("avgBox").checked === true) {
        avg1 = player.avg + " pts";
        avg2 = player2.avg + " pts";
        if (document.getElementById("beBox").checked === true) {
          avg1 += ", ";
          avg2 += ", ";
        }
      }
      
      let be1 = "";
      let be2 = "";
      if (document.getElementById("beBox").checked === true) {
        be1 = player.break_even + " BE";
        be2 = player2.break_even + " BE";
      }

      let stats1 = price1 + avg1 + be1;
      let stats2 = price2 + avg2 + be2;
      if (stats1.length > 0) {
        stats1 = "(" + stats1 + ")";
        stats2 = "(" + stats2 + ")";
      } 

      //let avg1 = "";
      myHtml += `
          <tr>
          <td class="home">
            ` + player.first_name + ` <b>` + player.last_name + `</b> &ensp;` + stats1 + `
          </td>
          <td class="number">
            ` + playerNum + `
          </td>
          <td class="away">
            ` + player2.first_name + ` <b>` + player2.last_name + `</b> &ensp;` + stats2 + `
          </td>
          </tr>
      `
      p += 2;
    }

    document.getElementById("myHtml").innerHTML = myHtml;
  }

  function checkChange() {
    getHtml();
  }

  return (
    <div className="App">
      <div className="loadingData">
        <br /><br />
        Getting data from database...
        <br /><br />
        <div className="spinner loading"></div>
      </div>
      <div className="checkbox">
        <form>
          <h3>Filters:</h3>
          <label>
            <input id="priceBox" name="checkPrice" type="checkbox" onChange={() => checkChange()} />
            Price
          </label>
          <br />
          <label>
            <input id="avgBox" name="avgPrice" type="checkbox" onChange={() => checkChange()} />
            Average this season
          </label>
          <br />
          <label>
            <input id="beBox" name="be" type="checkbox" onChange={() => checkChange()} />
            Break even
          </label>
        </form>
      </div>
      <br />
      <div id="myHtml"></div>
      <br /><br />
      
      
    </div>
  );
}

export default App;
